.leftDash h2 {
  font-family: Montserrat;
  font-size: 4vw;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  letter-spacing: -4px;
  width: 32vw;
  height: 9.5vw;
  margin-top: 2.5vw;
}
.redText {
  color: #77828c;
  font-family: Inter;
  font-size: 1vw;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  letter-spacing: -1px;
  margin-top: 0.5vw;
  margin-bottom: 1.5vw;
}
.redText b {
  color: #000;
  font-weight: 600;
}
.redText span {
  color: #ff4545;
  font-family: Inter;
  font-size: 1vw;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  letter-spacing: -1px;
}
.lighting {
  color: #626262;
  font-family: Inter;
  font-size: 1.25vw;
  font-style: normal;
  font-weight: 400;
  line-height: 160%; /* 38.4px */
  letter-spacing: -1.2px;
  margin-bottom: 1.5vw;
}
.buttonCalc {
  padding: 1vw 2.25vw;
  border-radius: 1000px;
  background: #0370f3;
  color: #fff;
  text-align: center;
  font-family: Inter;
  width: fit-content;
  cursor: pointer;
  transition: 0.5s;
  font-size: 1.25vw;
  font-style: normal;
  font-weight: 600;
  line-height: 160%; /* 38.4px */
  letter-spacing: -1.2px;
}
.dashboard {
  display: flex;
  justify-content: space-between;
}
.rightDash {
  background-size: cover;
  display: flex;
  width: 44.63vw;
  background-repeat: no-repeat;
  align-items: center;
  justify-content: center;
  font-family: Inter;
  font-size: 2.5vw;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  height: 44vw;
  letter-spacing: -2.4px;
}
.referal {
  display: flex;
  align-items: center;
  border-radius: 0.5vw;
  padding: 0.5vw 1vw;
  background: #f5f7f9;
  margin-top: 2.5vw;
  width: fit-content;
}
.referal img {
  width: 1.5vw;
  margin-right: 0.8vw;
}
.referal p {
  font-family: Inter;
  font-size: 1.1vw;
  font-style: normal;
  font-weight: 400;
  line-height: 160%; /* 35.2px */
  letter-spacing: -1.1px;
}
.stat {
  width: 33vw;
  margin-top: 2.5vw;
  padding-top: 2.5vw;
  border-top: 1px solid #e8edf2;
}
.buttonCalc {
  transition: 0.5s;
}
.buttonCalc:hover {
  background: #0052b7;
}
