.calc h2 {
  font-family: Montserrat;
  font-size: 3.3vw;
  font-style: normal;
  font-weight: 600;
  line-height: 120%; /* 78px */
  letter-spacing: -2.6px;
  padding-top: 2.5vw;
}
.leftCalc > input {
  border-radius: 1000px;
  width: 22vw;
  padding: 1vw 1.5vw;
  background: #f5f7f9;
  font-family: Inter;
  font-size: 1vw;
  font-style: normal;
  font-weight: 600;
  line-height: 100%; /* 18px */
  letter-spacing: -0.9px;
  border: none;
  outline: none;
  margin-top: 1vw;
  margin-bottom: 1.4vw;
}
.leftCalc {
  padding-right: 3vw;
  border-right: 1px solid #e8edf2;
  margin-right: 3vw;
}
.calc {
  display: flex;
}
.warn {
  display: flex;
  align-items: flex-end;
}
.warn div {
  width: 26vw;
  display: flex;
  align-items: flex-start;

  font-family: Montserrat;
  font-size: 0.7vw;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  letter-spacing: -0.7px;
  margin-left: 3vw;
  position: relative;
  top: -0.7vw;
}
.warn img {
  width: 1vw;
  margin-right: 0.1vw;
}
.numsic {
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin-bottom: 2vw;
}
.linik {
  width: 2px;
  background: #a8b0b7;
  height: 0.5vw;
  margin-bottom: 0.3vw;
}
.numsic > div {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 1vw;
}
.warn div p {
  color: #a8b0b7;
}
.lod {
  width: 47vw;
  margin-top: 1vw;
  margin-bottom: 1.4vw;
  cursor: pointer;
}
.warn div b {
  color: black;
}
.sale {
  color: #fff;
  text-align: center;
  font-family: Inter;
  border-radius: 1000px;
  background: #6ed375;
  padding: 0.8vw;
  box-shadow: 0px 9.866px 19.731px 0px rgba(110, 211, 117, 0.2);
  font-size: 2vw;
  transition: 0.3s;
  font-style: normal;
  font-weight: 600;
  cursor: pointer;
  line-height: 120%; /* 48px */
  letter-spacing: -2px;
  margin-bottom: 1.4vw;
}
.sale:hover {
  background: #48ac4f;
}
input[type="range"] {
  height: 1.9270833333333335vw;
  -webkit-appearance: none;
  margin: 0.5208333333333333vw 0;
  width: 100%;
}
input[type="range"]:focus {
  outline: none;
}
input[type="range"]::-webkit-slider-runnable-track {
  width: 100%;
  height: 1.9270833333333335vw;
  cursor: pointer;
  animate: 0.2s;

  background: #71d578;
  border-radius: 0.26041666666666663vw;
  border: 0.20833333333333334vw solid #f2f3f5;
}
input[type="range"]::-webkit-slider-thumb {
  border: 0.052083333333333336vw solid #ffffff;
  height: 3.125vw;
  width: 3.125vw;
  border-radius: 0.552083333333333336vw;
  background: url(./img/th.svg);
  background-size: cover;
  cursor: pointer;
  -webkit-appearance: none;
  margin-top: -0.8333333333333334vw;
}
input[type="range"]:focus::-webkit-slider-runnable-track {
  background: #71d578;
}
input[type="range"]::-moz-range-track {
  width: 100%;
  height: 1.9270833333333335vw;
  cursor: pointer;
  animate: 0.2s;
  box-shadow: 0.052083333333333336vw 0.052083333333333336vw
    0.052083333333333336vw #353535;
  background: #71d578;
  border-radius: 0.26041666666666663vw;
  border: 0.20833333333333334vw solid #f2f3f5;
}
input[type="range"]::-moz-range-thumb {
  border: 0.052083333333333336vw solid #ffffff;
  height: 3.125vw;
  width: 3.125vw;
  border-radius: 0.052083333333333336vw;
  background: #fff6f6;
  cursor: pointer;
}
input[type="range"]::-ms-track {
  width: 100%;
  height: 1.9270833333333335vw;
  cursor: pointer;
  animate: 0.2s;
  background: transparent;
  border-color: transparent;
  color: transparent;
}
input[type="range"]::-ms-fill-lower {
  background: #71d578;
  border: 0.20833333333333334vw solid #f2f3f5;
  border-radius: 0.052083333333333336vw;
}
input[type="range"]::-ms-fill-upper {
  background: #71d578;
  border: 0.20833333333333334vw solid #f2f3f5;
  border-radius: 0.052083333333333336vw;
  box-shadow: 0.052083333333333336vw 0.052083333333333336vw
    0.052083333333333336vw #353535;
}
input[type="range"]::-ms-thumb {
  margin-top: 0.052083333333333336vw;
  box-shadow: 0.052083333333333336vw 0.052083333333333336vw
    0.052083333333333336vw #353535;
  border: 0.052083333333333336vw solid #ffffff;
  height: 3.125vw;
  width: 3.125vw;
  border-radius: 0.052083333333333336vw;
  background: #fff6f6;
  cursor: pointer;
}
input[type="range"]:focus::-ms-fill-lower {
  background: #71d578;
}
input[type="range"]:focus::-ms-fill-upper {
  background: #71d578;
}
.calc h3 {
  color: var(--, #000);
  font-family: Montserrat;
  font-size: 1.9vw;
  font-style: normal;
  font-weight: 600;
  line-height: 120%; /* 43.2px */
  letter-spacing: -1.44px;
  margin-bottom: 1.4vw;
}
.economy {
  border-radius: 18.589px;
  background: #fff;
  padding: 1.5vw;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.3vw;
  cursor: pointer;
  transition: 0.5s;
  padding-left: 1.3vw;
  padding-right: 3.3vw;
  box-shadow: 0px 9.866px 19.731px 0px rgba(112, 151, 199, 0.2);
}
.gridDay {
  width: 47vw;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-column-gap: 1.7vw;
  grid-row-gap: 1.7vw;
}
.economy > p {
  font-family: Montserrat;
  font-size: 1.1vw;
  font-style: normal;
  font-weight: 600;
  line-height: 100%; /* 22px */
  letter-spacing: -1.1px;
}
.gridEm {
  padding: 1.5vw;
  border-radius: 1vw;
  transition: 0.3s;
  background: #fff;
  box-shadow: 0px 11.363px 22.727px 0px rgba(112, 151, 199, 0.2);
}
.gridEm p {
  font-family: Inter;
  font-size: 1.3vw;
  font-style: normal;
  font-weight: 600;
  margin-bottom: 0.6vw;
  line-height: 100%;
  letter-spacing: -1.267px;
}
.gridEm p span {
  color: #a8b0b7;
}
.gridEm > span {
  color: #a8b0b7;
  font-family: Montserrat;
  font-size: 0.65vw;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  letter-spacing: -0.633px;
}
.gridEm:hover {
  transform: translateY(-7px);
  cursor: pointer;
  box-shadow: 0px 5.363px 9.727px 0px rgba(112, 151, 199, 0.2);
}
.gridEm h4 {
  font-family: Montserrat;
  font-size: 1vw;
  margin-bottom: 0.8vw;
  font-style: normal;
  font-weight: 600;
  line-height: 100%; /* 19.581px */
  letter-spacing: -0.979px;
}
.economy div p {
  margin-bottom: 0.6vw;

  font-family: Inter;
  font-size: 1.1vw;
  font-style: normal;
  font-weight: 600;
  line-height: 100%; /* 22px */
  letter-spacing: -1.1px;
}
.economy div p:last-child {
  margin-bottom: 0;
}
.economy div p span {
  color: #a8b0b7;
}
.economy:hover {
  transform: translateY(-7px);
  box-shadow: 0px 5.866px 5.731px 0px rgba(112, 151, 199, 0.2);
}
.bit {
  width: 47vw;
  margin-top: 0.8vw;
}
