.leftProfile h2,
.rightrofile h2 {
  font-family: Montserrat;
  font-size: 4vw;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  letter-spacing: -4px;
  margin-left: 1vw;
  padding-top: 2.5vw;
}
.profile {
  display: flex;
  justify-content: flex-end;
}
.leftProfile > p,
.rightrofile > p {
  color: #77828c;
  font-family: Inter;
  font-size: 1vw;
  margin-left: 1vw;
  margin-top: 0.5vw;
  margin-bottom: 2.3vw;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  letter-spacing: -1px;
}
.leftProfile {
  width: 31vw;
}
.profile .inputAuth {
  margin-bottom: 1vw;
}
.rightrofile {
  border-left: 1px solid #e8edf2;
  padding-right: 5.6vw;
  padding-left: 7.3vw;
  margin-left: 7.5vw;
}
.rightrofile img {
  width: 12vw;
  cursor: pointer;
}
.leftProfile .flexInputs {
  align-items: center;
}
.savePassword {
  display: flex;
  align-items: center;
  background: #0370f3;
  color: #fff;
  padding: 0.8vw 1vw;
  border-radius: 1000px;
  font-size: 0.9vw;
  cursor: pointer;
  margin-top: 0.3vw;
  transition: 0.3s;
}
.savePassword img {
  margin-left: 0.5vw;
  width: 1vw;
}
.withButton input {
  width: 11vw !important;
}
.saveButton {
  display: flex;
  align-items: center;
  background: #0370f3;
  border-radius: 1000px;
  color: #fff;
  text-align: center;
  font-family: Inter;
  font-size: 1.2vw;
  font-style: normal;
  justify-content: center;
  font-weight: 600;
  line-height: 160%; /* 38.4px */
  letter-spacing: -1.2px;
  padding: 1vw;
  cursor: pointer;
  margin-top: 1.2vw;
  transition: 0.3s;
}
.saveButton:hover,
.savePassword:hover {
  background: #0150b1;
}
.saveButton img {
  width: 1.3vw;
  margin-left: 1vw;
}
.saveButton p {
  margin-left: 1.5vw;
}
