.header {
  position: fixed;
  width: 83vw;
  left: 17vw;
  top: 0;
  display: flex;
  align-items: center;
  padding: 1.5vw 2.5vw;
  justify-content: space-between;
  border-bottom: 1px solid #e8edf2;
  z-index: 3;
  background: #fff;
}
.search {
  display: flex;
  align-items: center;
  border-radius: 1000px;
  background: #f5f7f9;
  padding: 0.3vw;
  padding-left: 0.5vw;
  transition: 0.3s;
}
.search:hover img {
  transform: scale(1.2);
}
.search img {
  width: 1.5vw;
  transition: 0.3s;
  margin-right: 0.4vw;
}
.search input {
  border: none;
  outline: none;
  background: #f5f7f9;
  font-family: Inter;
  font-size: 0.95vw;
  font-style: normal;
  width: 24vw;
  font-weight: 400;
  line-height: 160%; /* 28.8px */
  letter-spacing: -0.9px;
}
.searchButton {
  background: #0370f3;
  color: #fff;
  text-align: center;
  font-family: Inter;
  font-size: 0.8vw;
  padding: 0.5vw 1.4vw;
  border-radius: 1000px;
  font-style: normal;
  font-weight: 600;
  line-height: 160%; /* 25.6px */
  letter-spacing: -0.8px;
  cursor: pointer;
  transition: 0.3s;
}
.searchButton:hover {
  background: #0152b5;
}
.headerProfile {
  display: flex;
  align-items: center;
  margin-left: 2vw;
  background: #f5f7f9;
  border-radius: 1000px;
  padding: 0.5vw;
}
.logout {
  color: #000;
  font-family: Inter;
  font-size: 0.95vw;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  letter-spacing: -0.9px;
  display: flex;
  align-items: center;
  margin-left: 1.5vw;
  cursor: pointer;
  transition: 0.3s;
}
.logout:hover {
  letter-spacing: 0.1vw;
}
.logout img {
  width: 2vw;
  margin-right: 0.5vw;
}
.header > img {
  margin-left: auto;
  width: 2.2vw;
  cursor: pointer;
  transition: 0.3s;
}
.header > img:hover {
  transform: scale(1.2);
}
.headerProfile .ava {
  width: 3vw;
  margin-right: 1vw;
}
.headerProfile p {
  color: var(--, #000);
  font-family: Inter;
  font-size: 1vw;
  font-style: normal;
  font-weight: 600;
  line-height: 100%; /* 20px */
  letter-spacing: -1px;
  margin-right: 1vw;
}
.settings {
  width: 1.5vw;
  margin-right: 0.7vw;
  cursor: pointer;
  transition: 0.3s;
}
.settings:hover {
  transform: scale(1.2);
}
